.filterName {
  font-weight: 700;
  margin-bottom: 5px;
  margin-top: 5px;
  color: white;
  margin-left: 10px;
}

.filterInput {
  margin-left: 15px;
  border-radius: 5px;
}
.filterInput:hover,
.filterInput:focus {
  border-color: rgb(69, 76, 161);
  background: rgba(229, 231, 253, 0.5);
  box-shadow: 0px 2px 2px rgba(74, 111, 190, 0.2),
    0px 1px 2px rgba(74, 111, 190, 0.2), 0px 3px 1px rgba(74, 111, 190, 0.2);
}
